a de su actividad\" (Rojas Cárdenas y Salomón Aguilar, 2016). En el subsector avícola, se identifican varios riesgos principales, como la manipulación de sustancias químicas, los cambios de temperatura dentro de los galpones, la exposición constante al ruido, a polvos y vapores, así como las posturas prolongadas fuera de los ángulos de confort.")]),_c('p',[_vm._v("En Colombia, se cuenta con el Decreto 1295 de 1994, la Resolución ICA y la Resolución 3651 de 2014 del Instituto Colombiano Agropecuario. A través de estas disposiciones se establecen los requisitos para la certificación de granjas avícolas bioseguras de postura y/o levante, además de dictar otras regulaciones. Los niveles de riesgo se clasifican en una escala del 1 al 5, siendo el sector avícola clasificado como nivel 2. No obstante, una empresa tiene la posibilidad de cotizar para diferentes niveles de riesgo, dependiendo de los distintos centros de trabajo que haya establecido. Por ejemplo, una compañía puede tener un centro de trabajo administrativo que cotice en el nivel de riesgo 1 y otro centro de trabajo productivo que cotice en el nivel de riesgo 4 (Rojas Cárdenas y Salomón Aguilar, 2016).")]),_c('p',[_vm._v("El trabajo en granjas o unidades avícolas conlleva diversos tipos de riesgos, tales como biológicos, químicos, higiénicos, psicológicos, ergonómicos, físicos, mecánicos, físicos no mecánicos, eléctricos, locativos y riesgos explosivos y de incendio.")])]),_c('img',{staticClass:"mb-4 mb-xl-0 col-xl-4 mx-auto order-1 order-xl-2",attrs:{"src":require("@/assets/curso/temas/tema1/img-3.png"),"data-aos":"fade-right"}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-md-10 col-lg-9 mb-4 mb-md-0",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"tarjeta color-primario p-3"},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_c('div',